
import { computed, defineComponent, ref, Ref } from "vue";
import {
  IonContent,
  IonPage,
  IonHeader,
  IonToolbar,
  IonButtons,
  IonMenuButton,
  IonSearchbar,
  IonGrid,
  IonRow,
  IonCol,
} from "@ionic/vue";

export default defineComponent({
  name: "MarketPlace",
  setup() {
    interface Story {
      id: string;
      name: string;
      cover: string;
    }

    const arr: Story[] = [
      { id: "1", name: "I spy", cover: "assets/cover-1.png" },
      { id: "2", name: "Benzin", cover: "assets/cover-2.png" },
      { id: "3", name: "The Deep", cover: "assets/cover-3.png" },
      { id: "4", name: "WEST", cover: "assets/cover-4.png" },
      { id: "5", name: "Bates Motel", cover: "assets/cover-5.png" },
    ];

    const stories: Ref<Story[]> = ref(arr);
    const searchString: Ref<string> = ref("W");
    const filteredStories = computed(() =>
      stories.value.filter((item) => item.name.toLowerCase().includes(searchString.value.toLowerCase()))
    );

    return { filteredStories , searchString};
  },
  components: {
    IonContent,
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonMenuButton,
    IonSearchbar,
    IonGrid,
    IonRow,
    IonCol,
  },
});
